<template name="user">

	<view class="things_user">
		<view class="bg-white">
			<!-- 用户信息 -->
			<view class="things_width_full userInfo">
				<image class="things_width_full topImage" src="./../../static/images/user/user_top_bg.png" mode="widthFix" />
				<!-- 用户资料 -->
				<view class="flex justify-center align-center user_box">
					<!-- 头像 -->
					<image class="user_cover" :src="userData.picPath ? userData.picPath : userInfo.userInfo.avatarUrl" mode="widthFix" />
					<view class="flex justify-start align-center user_box_right">
						<view class="name">{{userData.nickname}}</view>
						<view class="flex justify-start align-center score">
							<image class="score_icon" src="./../../static/images/global/score_icon.png" mode="widthFix" />
							<view class="score_num">{{userData.reputation}}</view>
							<view class="identity_text" v-if="userData && userData.is_verific == 1">{{userData.type | identity_format}}</view>
						</view>
					</view>
					<!-- 右箭头 -->
					<text class="cuIcon-right"></text>
				</view>
			</view>
			<!-- 积分 -->
			<view class="flex justify-between align-center integral_list">
				<view class="flex justify-center align-center integral_item" @tap="linkToGiftPoints">
					<view class="integral_num">{{userData.giftPoints}}</view>
					<view class="integral_name">馈赠分</view>
				</view>
				<view class="flex justify-center align-center integral_item" @tap="linkToConstructionPoints">
					<view class="integral_num">{{userData.buildPoints}}</view>
					<view class="integral_name">建设分</view>
				</view>
				<view class="flex justify-center align-center integral_item" @tap="linkToProfitPoints">
					<view class="integral_num">{{userData.ProfitPoints}}</view>
					<view class="integral_name">营利分</view>
				</view>
				<view class="flex justify-center align-center integral_item" @tap="linkToDividend">
					<view class="integral_num">{{userData.BonusPoints}}</view>
					<view class="integral_name">分红金</view>
				</view>
			</view>
			<!-- 订单 -->
			<view class="order_box">
				<view class="order_title">抢单订单</view>
				<view class="flex justify-between align-center order_type_box">
					<!-- 发布找车 -->
					<view class="bg-img flex justify-start align-center things_width_full order_find order_find_car" style="background-image:url('./../../static/images/publish/bg_car.png')" @tap="linkToOrderCar">
						<image class="order_find_icon" src="./../../static/images/publish/huoche.png" mode="widthFix" />
						<view class="order_find_title">我的抢车单</view>
					</view>
					<!-- 发布找货 -->
					<view class="bg-img flex justify-start align-center things_width_full order_find order_find_freight" style="background-image:url('./../../static/images/publish/bg_freight.png')" @tap="linkToOrderFreight">
						<image class="order_find_icon" src="./../../static/images/publish/huowu.png" mode="widthFix" />
						<view class="order_find_title">我的抢货单</view>
					</view>
				</view>
			</view>
			<!-- 其他功能 -->
			<view class="things_width_full fun_area">
				<view class="flex justify-start align-center other_title">其他功能</view>
				<view class="other_fun_list">
					<view class="things_width_full flex justify-between align-center other_fun_item" v-for="(item,index) in funData" :key="index" :data-id="item.id" @tap="linkToFun">
						<view class="other_fun_item_left flex justify-start align-center">
							<image class="other_fun_item_icon" :src="'./../../static/images/user/other_fun'+item.id+'.png'" mode="widthFix" />
							<view class="other_fun_item_name">{{item.name}}</view>
						</view>
						<image class="right_icon" src="./../../static/images/global/right_icon.png" mode="widthFix" />
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import { user_info_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
	import { identity_format } from '@/utils/filters.js';
	export default {
		name: "user",
		data() {
			return {
				userData: storage.get("userData") || {},
				userInfo: storage.get("userInfo") || {},
				funData: [
					{
						id: 1,
						name: '我的钱包'
					},
					{
						id: 2,
						name: '发布的需求'
					},
					{
						id: 3,
						name: '个人收藏'
					},
					{
						id: 4,
						name: '个人权益'
					},
					{
						id: 5,
						name: '个人等级'
					},
					{
						id: 6,
						name: '收集能量'
					},
					{
						id: 7,
						name: '个人信息'
					},
					{
						id: 8,
						name: '联系客服'
					},
				]
			};
		},
		watch: {
			'$store.getters.user_userData': function(val) {
				this.userData = val;
			},
			'$store.getters.user_userInfo': function(val) {
				this.userInfo = val;
			},
			'$store.getters.user_token': function(val) {
				this.getUserData();
			},
		},
		// 过滤器
		filters: {
			// 认证
			identity_format
		},
		methods: {
			...mapMutations([
				'modal',
				'user'
			]),
			...mapActions([
				'demoactions'
			]),
			/**
			* @description: 跳转到馈赠分
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToGiftPoints(e) {
				uni.navigateTo({
					url: `/pages/things/integral/giftPoints/info/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 跳转到建设分
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToConstructionPoints(e) {
				uni.navigateTo({
					url: `/pages/things/integral/constructionPoints/info/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 跳转到营利分
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToProfitPoints(e) {
				uni.navigateTo({
					url: `/pages/things/integral/profitPoints/info/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 跳转到分红金
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToDividend(e) {
				uni.navigateTo({
					url: `/pages/things/integral/dividend/info/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 跳转到我的抢车单
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToOrderCar(e) {
				uni.navigateTo({
					url: `/pages/things/user/order/car/list/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 跳转到我的抢货单
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToOrderFreight(e) {
				uni.navigateTo({
					url: `/pages/things/user/order/freight/list/index`,
					fail: function(err) {
						console.log(err);
					}
				});
			},
			/**
			* @description: 功能区跳转
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			linkToFun(e) {
				switch (Number(e.currentTarget.dataset.id)) {
					case 1:
						// 跳转钱包
						uni.navigateTo({
							url: `/pages/things/user/wallet/index`,
						});
						break;
					case 2:
						// 我发布的需求
						uni.navigateTo({
							url: `/pages/things/user/demand/list/index`,
						});
						break;
					case 3:
						// 个人收藏（智能填单）
						uni.navigateTo({
							url: `/pages/things/user/collect/list/index`,
						});
						break;
					case 4:
						// 个人权益
						uni.navigateTo({
							url: `/pages/things/user/interests/index`,
						});
						break;
					case 5:
						// 个人等级
						uni.navigateTo({
							url: `/pages/things/user/rank/index`,
						});
						break;
					case 6:
						// 收集能量
						uni.navigateTo({
							url: `/pages/things/user/energy/ball/index`,
						});
						break;
					case 7:
						// 个人信息
						uni.navigateTo({
							url: `/pages/things/user/info/index`,
						});
						break;
					case 8:
						// 联系客服
						uni.makePhoneCall({
							// 手机号
							phoneNumber: '15626236839', 
							// 成功回调
							success: (res) => {
								console.log('调用成功!')	
							},
							// 失败回调
							fail: (res) => {
								console.log('调用失败!')
							}
						});
						break;
					default:
						break;
				}
			},
			/**
			* @description: 获取个人信息
			* @param {*} e
			* @return {*}
			* @author: gitxinghui
			*/
			getUserData(e) {
				let that = this;
				user_info_api({},that).then(res=>{
					that.userData = res.data.userInfo;
					storage.set('userData',res.data.userInfo);
					that.user({name:'userData',value: res.data.userInfo});
				}).catch(err=>{
					console.log(err);
				})
			}
		},
		created() {
			// 请求个人信息
			this.getUserData();
		}
	}
</script>
<style lang="scss" scoped>
	@import "./user"
</style>
